<template>
   <div :class="{hello:true,paddingBottom:isIphoneX}" >
       <navheader :msg="{title:'12',color:'#3296FA',borderBottom:0,fontColor:1,rowColor:'./static/img/newback.png'}"  style="background-color: #3296FA;"></navheader>


            <!-- <navheader style="background-color: #3296FA;"  @startMethod="console.log(0)" :msg="{title1:'',logo:true,backImgUrl:'../../static/img/back1.png',title2:''}"  ></navheader> -->


    <mt-swipe  :show-indicators="false"  :auto="0"  style="height:100%;margin-top:-1px;"  >

          <mt-swipe-item class="container" v-for="(item,i) in choosedata" :key="i" >
            <mt-loadmore :top-method="loadTop"  ref="loadmore" style="height:100%;"  :topDistance="400" :top-status.sync="topStatus" >
            <div class="flex flex-column" >
                <div class="top flex flex-column"  >
                  <div style="width:100%;">
                       <img :src="item.picUrl" alt="">
                  </div>
                    <div class="wordContent" >
                    <p>{{item.title1}}</p>
                    <p v-if="item.title2.length>4">{{item.title2}}</p>
                     <p v-else >
                       <span v-for="(t,index) in item.title2" :key="index" style="width:100%;display:block;">{{t}}</span>
                     </p>

                    <p v-if="item.title3.length>4" :class="item.titlea?'tipcolor':'color'" style="line-height: .48rem;height: 0.48rem;" @click="!item.titlea&&login()"  >{{item.title3}}</p>
                    <p v-else :class="item.titlea?'tipcolor':'color'" @click="!item.titlea&&login()"  >
                      <span v-for="(tr,ir) in item.title3" :key="ir" style="width:100%;display:block;line-height: .48rem;height: 0.48rem;">{{tr}}</span>
                      </p>
                  </div>

                                    <!-- <div class="wordTip">
                    <p style="font-size: .4rem;line-height: .826667rem;">{{item.title4}}</p>
                    <p>{{item.title5}}</p>
                  </div> -->
                  </div>
                  <div class="wordTip">
                    <p style="font-size: .4rem;line-height: .826667rem;">{{item.title4}}</p>
                    <p>{{item.title5}}</p>
                  </div>
                 </div>

                               <div slot="top" class="mint-loadmore-top" >
    <span v-show="topStatus !== 'loading'" :class="{ 'rotate': topStatus === 'drop' }">...</span>
    <span v-show="topStatus === 'loading'">...</span>
  </div>
</mt-loadmore>
          </mt-swipe-item>

    </mt-swipe>



</div>

</template>

<script>
import {getUrlParams,getNative,getUrlName} from '../../static/js/checkcode.js'
import navheader from "./appheadera.vue"
export default {
  name: 'conclude',
  data () {
    return {
    //  title:"",
      msg: 'conclude',
      color:false,
      datas:[[{picUrl:"./static/img/guidelist11.jpg",title1:"",title2:"APP;",title3:"",title4:"1/4",title5:""},{picUrl:"./static/img/guidelist12.jpg",title1:"",title2:"APP;",title3:"",title4:"2/4",title5:""},{picUrl:"./static/img/guidelist13.jpg",title1:"",title2:"SN;",title3:"",title4:"3/4",title5:""},{picUrl:"./static/img/guidelist14.jpg",title1:"",title2:"APP60s;",title3:"",title4:"4/4",title5:""}],

      [{picUrl:"./static/img/guidelist21.jpg",title1:"/",title2:";",title3:"",title4:"1/2",title5:""},{picUrl:"./static/img/guidelist22.jpg",title1:"/",title2:"",title3:"APP",title4:"2/2",title5:"",titlea:true}],

      [{picUrl:"./static/img/guidelist31.jpg",title1:"",title2:"APP ",title3:"",title4:"1/3",title5:""},{picUrl:"./static/img/guidelist32.jpg",title1:"",title2:"",title3:"",title4:"2/3",title5:""},
      {picUrl:"./static/img/guidelist33.jpg",title1:"",title2:"SN",title3:"",title4:"3/3",title5:""}],

      [{picUrl:"./static/img/guidelist41.jpg",title1:"",title2:"APP",title3:"",title4:"1/2",title5:""},{picUrl:"./static/img/guidelist42.jpg",title1:"",title2:"",title3:"",title4:"2/2",title5:""}],

      [{picUrl:"./static/img/guidelist51.jpg",title1:"",title2:"APP",title3:"",title4:"1/3",title5:""},{picUrl:"./static/img/guidelist52.jpg",title1:"",title2:"+",title3:"",title4:"2/3",title5:""},
      {picUrl:"./static/img/guidelist53.jpg",title1:"",title2:"",title3:"",title4:"3/3",title5:""}],

      [{picUrl:"./static/img/guidelist61.jpg",title1:"",title2:"APP ",title3:"",title4:"1/3",title5:""},{picUrl:"./static/img/guidelist62.jpg",title1:"",title2:'""""',title3:"",title4:"2/3",title5:""},
      {picUrl:"./static/img/guidelist63.jpg",title1:"",title2:"",title3:"",title4:"3/3",title5:""}],

      [{picUrl:"./static/img/guidelist71.jpg",title1:"",title2:"APP ",title3:"",title4:"1/2",title5:""},{picUrl:"./static/img/guidelist72.jpg",title1:"",title2:"4006858885",title3:"",title4:"2/2",title5:""}],

      [{picUrl:"./static/img/guidelist81.jpg",title1:"",title2:"APP",title3:"",title4:"1/3",title5:""},{picUrl:"./static/img/guidelist82.jpg",title1:"",title2:";",title3:"",title4:"2/3",title5:""},{picUrl:"./static/img/guidelist83.jpg",title1:"",title2:"",title3:"",title4:"3/3",title5:""}],

      [{picUrl:"./static/img/guidelist91.jpg",title1:"",title2:"APP",title3:"",title4:"1/2",title5:""},{picUrl:"./static/img/guidelist92.jpg",title1:"",title2:["","A.	 " ,"B.	"],title3:"",title4:"2/2",title5:"",titlea:true}],

      [{picUrl:"./static/img/guidelist101.jpg",title1:"",title2:"APP",title3:"",title4:"1/4",title5:""},{picUrl:"./static/img/guidelist102.jpg",title1:"",title2:"",title3:['A.	','B.	'],title4:"2/4",title5:"",titlea:true},
      {picUrl:"./static/img/guidelist103.jpg",title1:"",title2:"",title3:"",title4:"3/4",title5:""},{picUrl:"./static/img/guidelist104.jpg",title1:"",title2:"",title3:"",title4:"4/4",title5:"",titlea:true},],

      [{picUrl:"./static/img/guidelist111.jpg",title1:"",title2:"APP",title3:"",title4:"1/2",title5:""},{picUrl:"./static/img/guidelist112.jpg",title1:"",title2:"",title3:"",title4:"2/2",title5:"",titlea:true}],
      ],
				backImgUrl:"static/img/back.png",
				articalData:{},
				artical_img:"",
				isActive:false,
        isIos:false,
        choosedata:{},
        isIphoneX:false,
        topStatus:'123'

    }
  },
  components:{
    navheader
    },
  methods: {
    jump:function(a){
       this.$router.push("/"+a)
    },
    move:function(a){
       var li=document.querySelectorAll("img")[0];
       if(a==0){li.className='moveleft';}else{li.className='moveright';}
    },
    login:function(){
      console.log("")
    },
    gg:function(){
      console.log("aaa")
    },
    loadTop:function() {
      // alert(1)
        this.$refs.loadmore.onTopLoaded();
      //    alert(2)
        this.$refs.loadmore.style.transform='translate3d(0px, 0px, 0px)';
      },
    loadBottom:function() {
  this.$refs.loadmore.onBottomLoaded();
      }

  },
  mounted:function(){
        //          if(getUrlName("hwBottomHeight")!=0){
        //   document.querySelector(".artical").style.paddingBottom=getUrlName("hwBottomHeight")+"px" ;
        // }
  },
  created:function(){
    if(/iphone/gi.test(navigator.userAgent) && (screen.height == 812 && screen.width == 375)){
      this.isIphoneX=true;
    }


    // this.indexa=this.$route.query.index||JSON.parse(localStorage.guideNum);
    this.choosedata=this.datas[this.$route.query.index]||JSON.parse(localStorage.guidelistData)
        localStorage.guidelistData=JSON.stringify(this.choosedata);
    // console.log("",this.choosedata.length)
    if(this.$route.query.index==1){this.color=true;localStorage.guidelistColor=JSON.stringify(this.color);}
    else if(this.$route.query.index==undefined){this.color=JSON.parse(localStorage.guidelistColor);
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.hello{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* border:2px solid yellow;
  box-sizing: border-box; */
  /* overflow-x: scroll;
  -webkit-overflow-scrolling:touch; */
}
.new {
  /* height: 100%; */
  flex: 1;
  width: 100%;
  list-style-type: none;
  padding: 0;
  overflow-x: scroll;
  -webkit-overflow-scrolling:touch;
}
.container{
  height: 100%;
  width: 100%;
  background-color: white;
}


.top img{
  display: block;
  /* height: 7.653333rem; */
  width: 100%;
}
.wordContent{
  width: 100%;
  /* text-align: center; */
  padding: 0 .56rem;
  height: 6.466667rem;
  /* height: 6.666667rem; */
  /* padding: .746667rem; */
}
.wordContent>p:nth-child(1){
  font-size: .64rem;
  color: #222222;
  margin: .266667rem  auto 0;
  height: 1.013333rem;
  line-height:1.013333rem;
}
.wordContent>p:nth-child(2){
  font-size: .426667rem;
  color: #888888;
  /* height: .693333rem; */
  line-height:.693333rem;
}
.wordContent>p:nth-child(3){
  margin-top: .266667rem;
  height: .693333rem;
  line-height:.693333rem;


}
.color{color:#3296FA;font-size: .426667rem;}
.tipcolor{color:#fb308b;font-size:.32rem;}
.wordTip{
  /* position: absolute;
  bottom: 0.8rem; */
  flex: none;
  text-align: center;
  width: 100%;
  color: #888888;
  margin-bottom: .8rem;
}
.moveleft{

  animation:myfirst 1s linear;
  animation-fill-mode: forwards;

}
.moveright{

  animation:myfirst2 1s linear;
  animation-fill-mode: forwards;

}
@keyframes myfirst
{
0%   {margin-left:0px;}
100% {margin-left:-100%;}
}
@keyframes myfirst2
{

0% {margin-left:-100%;}
100%   {margin-left:0px;}
}


	.clickButton span{
    /* padding:0.2rem 0.4rem; */
    padding:0 0.4rem;
	}
	.clickButton .padding_right{
		padding-right:0;
	}
	.clickButton span u{
		height: 0.693333rem;
		width: 0.693333rem;
		background: rgba(255,255,255,0.8);
		font-size: 0.48rem;
		text-align: center;
    line-height: 0.693333rem;
    color: black;
	}
	/* .clickButton span .span_img{
		width: 60%;
		height: 60%;
	} */
	.radius{
		border-radius: 50%;
	}

</style>



